import { useEffect, useMemo, useRef, useState } from 'react';
export const useModel = (store, strict = false) => {
    const [_, set_] = useState(false);
    const init = useRef(false);
    useEffect(() => {
        if (init.current) {
            return;
        }
        init.current = true;
        store.addStateController(set_);
    }, [store]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    return useMemo(() => store.createModel(strict), [store, _]);
};
