import { useCallback, useMemo } from 'react';
import { useModel } from './useModel';
export const useController = (store) => {
    const controller = useMemo(() => store.createController(), [store]);
    const model = useCallback(() => {
        return store.model();
    }, [store]);
    return [model, controller];
};
export const useData = (store) => {
    const data = useModel(store);
    const controller = useMemo(() => store.createController(), [store]);
    return [data, controller];
};
