export const store = { v: 1 };
const handler = {
    set() {
        console.warn(
        // eslint-disable-next-line quotes
        "Don't change store of a model directly. Use setModelValue instead.");
        return true;
    }
};
const toDeepProxy = (obj, handler) => {
    const f = (value, handler) => {
        for (let key in value) {
            if (value[key] instanceof Object) {
                toDeepProxy(value[key], handler);
                value[key] = new Proxy(value[key], handler);
            }
        }
    };
    f(obj, handler);
    return new Proxy(obj, handler);
};
export class Store {
    constructor(value) {
        this.stateControllers = [];
        this.value = value;
    }
    createController() {
        // eslint-disable-next-line no-unused-vars
        const controller = (value) => {
            if (value.call) {
                this.value = value(this.value);
            }
            else {
                this.value = value;
            }
            this.stateControllers.forEach((c) => c((v) => !v));
        };
        return controller;
    }
    createModel(strict = false) {
        if (typeof this.value === 'object' && strict) {
            toDeepProxy(this.value, handler);
            return new Proxy(this.value, handler);
        }
        return this.value;
    }
    addStateController(controller) {
        this.stateControllers.push(controller);
    }
    model() {
        return this.value;
    }
}
